 profundizar en las respuestas; éstas no siempre pueden ser tajantes y categóricas (Parada & Moreno Garzón, 1999).")]),_c('li',{staticClass:"d-flex",attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"fas fa-check",staticStyle:{"color":"#007D78"}}),_vm._v(" Es conveniente utilizar frases de transición. En el conjunto de preguntas, casi siempre existe un grupo que se refiere a temas semejantes. Al terminar con un tema y pasar al siguiente, conviene echar mano de algunas frases de transición: “bueno”, “veamos ahora”, “muy bien...”, “¿le parece que sigamos con...?”. Estas expresiones “de descanso” ayudan también a ubicar psicológicamente al interrogado para no sentirse \"agotado ni agobiado” (Parada & Moreno Garzón, 1999).")]),_c('li',{staticClass:"d-flex",attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"fas fa-check",staticStyle:{"color":"#007D78"}}),_vm._v(" Debe dejarse constancia escrita de los cambios introducidos eventualmente en el cuestionario. Si bien por regla general no deben introducirse cambios en el cuestionario, podría presentarse algún motivo especial que los hiciera indispensables. El cambio podría ser alteración de orden de las preguntas, reemplazo de algunas palabras, formulación de la pregunta, en otros términos, etc. En estos casos, cualquiera que haya sido el motivo (aun el propio descuido del encuestador) el cambio debe ser anotado en el formulario o registro correspondiente y/o en el informe escrito posteriormente. Es necesario hacerlo así porque, como es obvio, la comparabilidad de las respuestas se basa en la identidad de las preguntas, cuando se realiza la misma entrevista a personas de forma individualizada (Parada & Moreno Garzón, 1999).")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mt-5"},[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/uZlcsWBW87w","title":"Entrevista","frameborder":"0","allow":"ac